import ndarray from 'ndarray';
import ops from 'ndarray-ops';
import rotate from 'image-rotate';
import pngjs from 'pngjs';
import Promise from 'bluebird';

const centerTpl = '';
const cornerTpl = '';
const borderTpl = '';

const maskTpls = [
    // Corner mask
    {
        name: 'leftTopCorner',
        src: cornerTpl,
        angle: 0
    },
    {
        name: 'leftBottomCorner',
        src: cornerTpl,
        angle: Math.PI / 2
    },
    {
        name: 'rightBottomCorner',
        src: cornerTpl,
        angle: 2 * Math.PI / 2
    },
    {
        name: 'rightTopCorner',
        src: cornerTpl,
        angle: 3 * Math.PI / 2
    },
    // Border mask
    {
        name: 'topBorder',
        src: borderTpl,
        angle: 0
    },
    {
        name: 'leftBorder',
        src: borderTpl,
        angle: Math.PI / 2
    },
    {
        name: 'bottomBorder',
        src: borderTpl,
        angle: 2 * Math.PI / 2
    },
    {
        name: 'rightBorder',
        src: borderTpl,
        angle: 3 * Math.PI / 2
    },
    // Center mask
    {
        name: 'center',
        src: centerTpl,
        angle: 0
    }
];

let masks = (async () => {
    let _masks = {};

    await Promise.each(maskTpls, async (maskTpl) => {
        const decodedData = Uint8Array.from(atob(maskTpl.src), c => c.charCodeAt(0));

        const parser = new pngjs.PNG();

        await new Promise((rs, rj) => {
            parser.parse(decodedData, (err, data) => {
                if (data) {
                    rs(data);
                } else {
                    rj(err);
                }
            });
        });

        const angle = maskTpl.angle;
        const mask = ndarray(new Float32Array(parser.data), [128, 128, 4]).pick(null, null, 0);
        const rotatedMask = ndarray(new Float32Array(128 * 128), [128, 128]);

        if (angle !== 0)
            rotate(rotatedMask, mask, angle);
        else
            ops.assign(rotatedMask, mask);

        ops.divseq(rotatedMask, 255.0);

        masks[maskTpl.name] = rotatedMask;
    });

    return _masks;
})();

export default masks;